
import { Input, Validators } from "./Input";

export function AddingFormInputs({ errors, register }) {
    return (
        <>
            <Input type={'name'} inputType={'text'} span={''} errors={errors}
                register={register('name', {
                    required: true,
                    pattern: Validators.text.pattern
                })} />
            <Input type={'population'} inputType={'number'} span={''} errors={errors}
                register={register('population', {
                    required: true,
                    pattern: Validators.number.pattern
                })} />
            <Input type={'ins'} inputType={'number'} span={'- '} errors={errors}
                register={register('ins', {
                    required: true,
                    pattern: Validators.number.pattern
                })} />
            <Input type={'outs'} inputType={'number'} span={'- '} errors={errors}
                register={register('outs', {
                    required: true,
                    pattern: Validators.number.pattern
                })} />
        </>
    )
}

